import { Injectable } from '@angular/core';
import { Resolve, RouterStateSnapshot, ActivatedRouteSnapshot } from '@angular/router';
import { UserManagerService } from '../user-manager.service';
import { empty } from 'rxjs';
import { catchError } from 'rxjs/operators';

@Injectable()
export class UserDetailResolver implements Resolve<any> {
  constructor(private userManagerService: UserManagerService) { }

  resolve(route: ActivatedRouteSnapshot, _state: RouterStateSnapshot) {
    console.log('router', route.params);
    return this.userManagerService.getUser(route.params['id']).pipe(
      catchError((error) => {
        return empty();
      })
    );
  }
}
