import { Injectable } from '@angular/core';
import { Observable, BehaviorSubject } from 'rxjs';
import { UserService } from '../core/user.service';
import { TableState } from '../core/table-state';

@Injectable()
export class UserManagerService {

  public state: TableState = {
    page: 1,
    pageSize: 10,
    searchTerm: '',
    sortColumn: '',
    sortDirection: ''
  }

  private _total$ = new BehaviorSubject<number>(1);
  get total$(): Observable<number> {
    return this._total$.asObservable();
  }

  private originalList = [];
  private _userList$ = new BehaviorSubject<any[]>(null);
  get userList$(): Observable<any[]> {
    return this._userList$.asObservable();
  }

  constructor(private userService: UserService) { }

  createUser(user: any): Observable<any> {
    // const user = {
    //   name: user['name'],
    //   email: user['email'],
    //   password: user['password'],
    //   address: user['address'],
    //   city: user['city'],
    //   mobile: user['mobile'],
    //   status: user['status']
    // };
    return this.userService.createUser(user);
  }

  updateUser(user: any): Observable<any> {
    // const user = {
    //   _id: user['_id'],
    //   name: user['name'],
    //   email: user['email'],
    //   address: user['address'],
    //   city: user['city'],
    //   mobile: user['mobile'],
    //   status: user['status']
    // };
    return this.userService.updateUser(user);
  }

  deleteUser(id: string): Observable<any> {
    return this.userService.deleteUser(id);
  }

  getUser(id: string): Observable<any> {
    return this.userService.getUser(id);
  }


  getUserList(): void {
    this.userService.getUserList()
      .subscribe((data: any[]) => {
        console.log('Got Users for you: ', data);

        this._userList$.next(data);
        this._total$.next(data.length);
        this.originalList = data;
      });
  }

  searchText(): void {
    if (!this.state.searchTerm) {
      this._userList$.next(this.originalList);
      this._total$.next(this.originalList.length)
    } else {
      const searchTerm = this.state.searchTerm.toLocaleLowerCase();
      const filteredList = this.originalList.filter((item: any) => item['last_name'] && item['last_name'].toLocaleLowerCase().includes(searchTerm) ||
        item['email'] && item['email'].toLocaleLowerCase().includes(searchTerm) ||
        item['first_name'] && item['first_name'].toLocaleLowerCase().includes(searchTerm) ||
        item['mobile'] && item['mobile'].toLocaleLowerCase().includes(searchTerm) ||
        item['status'] && item['status'].toLocaleLowerCase().includes(searchTerm));
      this._userList$.next(filteredList);
      this._total$.next(filteredList.length);
    }
  }
}